.btn {
  font-size: 20px;
  line-height: 1;
  padding: 8px 16px;
  box-sizing: border-box;
  text-decoration: none;
  text-transform: capitalize;
  margin-top: 16px;
  margin-bottom: 16px;
  max-height: 40px;
}

.btn-primary {
  background: #f12c3e;
  color: #f9f5f2;
  border: 1px solid #f12c3e;
  border-radius: 3px;
}

.btn-primary:hover {
  background: #f5616e;
}

.btn-secondary {
  background: #003359;
  color: #f9f5f2;
  border: 1px solid #003359;
  border-radius: 3px;
}

.btn-secondary:hover {
  background: #406683;
}

.btn-skeleton {
  background: none;
  color: #f9f5f2;
  border: 2px solid #f9f5f2;
  border-radius: 3px;
  padding: 7px 17px;
}

.btn-skeleton:hover {
  background: #f9f5f2;
  color: #003359;
}

.btn-skeleton-navy {
  background: none;
  color: #003359;
  border: 2px solid #003359;
  border-radius: 3px;
  padding: 7px 17px;
}

.btn-skeleton-navy:hover {
  background: #003359;
  color: #f9f5f2;
}

.callout {
  min-height: 320px;
  padding: 60px auto 50px;
}

.callout-bg {
  position: absolute;
  width: 100%;
  margin: 0 auto;
  background-size: cover;
  background-position: center center;
  min-height: 320px;
}

.callout-blue-fliter {
  filter: grayscale(100%) brightness(30%) contrast(130%) sepia(100%) hue-rotate(170deg) saturate(300%);
}

.callout-bw-fliter {
  filter: grayscale(100%) brightness(80%) contrast(130%);
}

.callout-content {
  position: relative;
  box-sizing: border-box;
  padding: 15px;
  font-size: 2vh;
  align-self: center;
}

@media (min-width: 968px) {
  .callout-content {
    padding: 10%;
  }
}

.callout-content h1 {
  font-size: 2.5rem;
  padding-bottom: 32px;
}

.callout-content p {
  padding-bottom: 32px;
}

.hero-bg {
  position: absolute;
  width: 100%;
  margin: 0 auto;
  background-size: cover;
  background-position: center center;
  min-height: 812px;
}

.hero-blue-fliter {
  filter: grayscale(100%) brightness(30%) contrast(130%) sepia(100%) hue-rotate(170deg) saturate(300%);
}

.hero-bw-fliter {
  filter: grayscale(100%) brightness(80%) contrast(130%);
}

.hero-content {
  position: relative;
  box-sizing: border-box;
  padding: 15px;
  font-size: 2vh;
  align-self: center;
}

@media (min-width: 968px) {
  .hero-content {
    padding: 10%;
  }
}

.hero-content h1 {
  font-size: 4rem;
  padding-bottom: 32px;
}

.nav {
  flex-wrap: nowrap;
  justify-content: space-between;
  font-size: 10px;
}

.nav-logo {
  flex-basis: 20%;
  padding-left: 16px;
}

.nav-logo img {
  display: inline;
  margin: 24px auto 0;
  vertical-align: bottom;
}

@media (min-width: 1224px) {
  .nav-logo img {
    vertical-align: middle;
  }
}

.nav-links {
  flex-basis: 75%;
  flex-wrap: wrap-reverse;
  justify-content: flex-end;
}

.nav-item {
  font-size: 20px;
  align-content: center;
  box-sizing: border-box;
}

.nav-link {
  vertical-align: middle;
  margin: 20px 20px 0 20px;
  text-decoration: none;
  color: #003359;
}

@media (min-width: 1224px) {
  .nav-link {
    margin: 24px 24px 0 24px;
  }
}

.nav-link:hover {
  margin-left: 0;
}

@media (min-width: 1224px) {
  .nav-link:hover {
    margin-left: 4px;
  }
}

.nav-link:hover::before {
  content: url(../../../../assets/img/JJ-plus-v1.svg);
  width: 10px;
  height: 10px;
}

